.cards{
    display: flex;
    justify-content: space-around;
    
}
.card-details{
 display: flex;
 flex-direction: column;
 font-family: "Poppins" sans-serif;
 position: relative;
}
.rates{
    display: flex;
    align-items: center;
    font-size: 14px;
    gap: 5px;
    margin-top: 12px;
}
.rates img{
    width: 14px;
}
.rates span:nth-child(3){
    color: #918E9B;
}
.card-details p{
    font-size: 12px;
}
.price{
     font-weight: bold;
}
.badge{
    position: absolute;
    left: 10px;
    top:15px;
    background-color: #fff;
    font-size: 11px;
    text-align: center;
    border-radius: 5px;
    padding: 10px;
    
    
}